@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,700;1,400&display=swap');
.contact {
	color: #000;
	padding: 5px;
	margin: 8px 0px;
	box-shadow: 0px 0px 3px 1px rgb(34 60 80 / 20%);
}

.contact a {
	display: flex;
	text-decoration: none;
	color: #000;
}
.contact__name {
	flex-wrap: wrap;
	display: flex;
	align-items: center;
}

.contact__container {
	flex-wrap: wrap;
	display: flex;
	align-items: center;
}

.contact__first-name {
	font-size: 16px;
	font-family: 'Open Sans', sans-serif;
	margin: 0px 5px;
}

.contact__last-name {
	font-size: 16px;
	font-family: 'Open Sans', sans-serif;
	margin: 0px 5px;
}

.gender {
	margin: 0px 5px;
}
.gender-icon {
	fill: #4caf50;
	width: 18px;
	height: 18px;
	margin-top: 5px;
}

.phone {
	margin-left: 5px;
	padding: 5px 0px;
	color: #9e9e9e;
	font-family: 'Ubuntu', sans-serif;
	font-size: 14px;
}

.human-icon {
	fill: #ff9800;
	margin-top: 5px;
	width: 60px;
	height: 60px;
	border-bottom: 2px solid #ff9800;
}

.contact__content {
	flex: 1 1 auto;
}

.info__contact {
	text-align: center;
	padding: 10px;
	background-color: rgb(245, 124, 0, 0.3);
}

.info__title {
	color: #263238;
	font-family: 'Ubuntu', sans-serif;
	font-weight: 800;
}

.info__body {
	font-size: 18px;
	color: #795548;
	font-weight: 700;
}
