body {
	background-color: #009688;
}

nav {
	z-index: 10;
	/* position: fixed; */
	width: 100%;
	/* border-bottom: 1px solid #fff; */
}
.menu__item .selected {
	background-color: #03a9f4;
	color: #fff;
}

.menu {
	padding: 20px;
	background-color: #fff;
	margin: 0px;
	display: flex;
	justify-content: center;
}

.menu__item {
	padding: 20px 0px;
	list-style: none;
	margin: 0px 10px;
}
.menu__item a {
	padding: 20px;
	font-family: 'Oxygen', sans-serif;
	font-weight: 700;
	color: #03a9f4;
	text-transform: uppercase;
	text-decoration: none;
	transition: 0.2s;
}

.menu__item a:hover {
	background-color: #03a9f4;
	color: #fff;
	transition: 0.2s;
}

@media (max-width: 460px) {
	.menu {
		flex-direction: column;
		align-items: center;
	}
}

