	letter-spacing: 5px;
	color: #9e9e9e;
	font-size: 14px;
	font-family: 'Open Sans', sans-serif;
}

.search input:-ms-input-placeholder {
	text-align: center;
	letter-spacing: 5px;
	color: #9e9e9e;
	font-size: 14px;
	font-family: 'Open Sans', sans-serif;
}

.search input::placeholder {
	text-align: center;
	letter-spacing: 5px;
	color: #9e9e9e;
	font-size: 14px;
	font-family: 'Open Sans', sans-serif;
}
.filter {
	color: #fff;
	font-size: 16px;
	font-family: 'Open Sans', sans-serif;
}

.search input {
	color: #212121;
	border: 1px solid #fff;
	font-size: 16px;
	font-family: 'Open Sans', sans-serif;
	padding: 10px;
	outline: none;
}

.search input:focus {
	border: 1px solid #9e9e9e;
	box-shadow: 0 0 12px #9e9e9e;
}

.checkbox-block {
	position: relative;
	text-align: center;
	padding: 5px 0px;
}
.custom-checkbox {
	cursor: pointer;
	font-family: 'Open Sans', sans-serif;
	font-size: 16px;
	margin-left: 10px;
}
.custom-checkbox::before {
	content: '';
	position: absolute;
	left: 0;
	width: 22px;
	height: 22px;
	border: 1px solid #fff;
}
.custom-checkbox::after {
	content: '';
	position: absolute;
	top: 8px;
	left: 2px;
	width: 4px;
	height: 12px;
	border-top: 2px solid transparent;
	border-left: 2px solid transparent;
	border-right: 2px solid #fff;
	border-bottom: 2px solid #fff;
	-webkit-transform: rotate(37deg);
	transform: rotate(37deg);
	-webkit-transform-origin: 100% 100%;
	transform-origin: 100% 100%;
	opacity: 0;
}
.checkbox:checked + .custom-checkbox::after {
	opacity: 1;
}
.checkbox:checked + .custom-checkbox::before {
	background-color: #2196f3;
	transition: 0.5s;
}
.checkbox {
	opacity: 0;
}

.checkbox__row {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding: 5px 0px;
}

