	color: #9e9e9e;
	font-family: 'Ubuntu', sans-serif;
	font-size: 14px;
}

.human-icon {
	fill: #ff9800;
	margin-top: 5px;
	width: 60px;
	height: 60px;
	border-bottom: 2px solid #ff9800;
}

.contact__content {
	flex: 1 1 auto;
}

.info__contact {
	text-align: center;
	padding: 10px;
	background-color: rgb(245, 124, 0, 0.3);
}

.info__title {
	color: #263238;
	font-family: 'Ubuntu', sans-serif;
