.Home {
	margin: 0 auto;
	margin-top: 120px;
	max-width: 1180px;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	overflow: hidden;
}

.Home__title {
	position: relative;
	width: 100%;
	padding: 20px 0px;
	background: #fff;
	color: #ff6f00;
	text-align: center;
}

.Home__title p {
	margin: 0px;
	text-align: center;
	font-size: 50px;
	font-family: 'Architects Daughter', cursive;
}

.Home__photo {
	position: relative;
	max-height: 520px;
}

.Home__photo::before {
	position: absolute;
	content: '';
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgb(18, 18, 16, 0.5);
}

@media (max-width: 460px) {
	.Home__title p {
		font-size: 28px;
	}
}

.Posts {
	max-width: 760px;
	width: 100%;
	padding: 10px;
	box-sizing: border-box;
	margin: 0 auto;
}

.post {
	background-color: #fff;
	padding: 12px;
	display: flex;
	box-shadow: 0 0 7px #fff;
	margin: 20px 0px;
	border: 1px solid #fff;
}

.avatar {
	flex: 0 0 auto;
}

.oficial-icon {
	width: 24px;
	fill: #4caf50;
}

.avatar img {
	height: 50px;
	width: 50px;
	border-radius: 50%;
	display: block;
	object-fit: cover;
}

.post__container {
	padding-left: 10px;
}

.post__header {
	position: relative;
	font-size: 18px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-family: 'Roboto', sans-serif;
}

.post__header-wrapper {
	display: flex;
}

.post-button-icon {
	width: 20px;
	cursor: pointer;
}

.post-button-icon:hover {
	fill: #e53935;
}

.author-name {
	color: #2196f3;
	display: flex;
	flex-wrap: wrap;
	font-weight: 700;
}

.author-nickname {
	color: #9e9e9e;
	padding: 0px 5px;
}

.author-nickname::after {
	content: '·';
	padding-left: 5px;
}
.post__date {
	color: #9e9e9e;
}

.post__text {
	color: #000;
	margin: 10px 0px;
	font-family: 'Roboto', sans-serif;
	font-size: 18px;
	font-weight: 400;
}

.post__img {
	width: 100%;
	object-fit: cover;
	border-radius: 24px;
}

.post__footer {
	display: flex;
	justify-content: space-around;
	padding: 20px 0px;
}

.icons {
	width: 26px;
}
.post__answer,
.post__retweet,
.post__like,
.post__share {
	display: flex;
	align-items: center;
	cursor: pointer;
}

.post__answer:hover .icons {
	fill: #2196f3;
}

.post__retweet:hover .icons {
	fill: #9c27b0;
}

.post__like:hover .icons {
	fill: #f44336;
}

.post__share:hover .icons {
	fill: #2196f3;
}

@media (max-width: 460px) {
	.post__header {
		font-size: 16px;
	}
	.post__container {
		padding-left: 5px;
	}
}

.photo__wrapper {
	max-width: 1200px;
	width: 100%;
	margin: 0 auto;
	padding: 50px 0px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.photo__block {
	position: relative;
	/* flex: 1 1 33.33%; */
	max-width: 360px;
	max-height: 300px;
	width: 100%;
	margin: 10px;
}

.photo {
	display: block;
	position: relative;
	padding: 5px;
	background: aliceblue;
	object-fit: cover;
	max-width: 360px;
	width: 100%;
	height: 300px;
	box-sizing: border-box;
}

